import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { NavigationExtras, Router } from '@angular/router';
import { ItemProfileTypes } from '../../../types/item-profile-type';
import { IProfessionalExperience } from '../../../../@store/user/user.model';
import { UserState } from '../../../../@store/user/user.state';
import { Store } from '@ngxs/store';
import { Subscription } from 'rxjs';
import { Utils } from '../../../../@global/utils';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-experience',
  templateUrl: './experience.page.html',
  styleUrls: ['./experience.page.scss'],
})
export class ExperiencePage implements OnInit, OnDestroy {
  @Input() editable: boolean;
  public items: IProfessionalExperience[] = [];
  private subscription$: Subscription;

  constructor(private router: Router, private store: Store, private translateService: TranslateService) {}

  ngOnInit() {
    this.subscription$ = this.store.select(UserState.getExperienceProfile).subscribe((data) => {
      this.items = data;
    });
  }

  public getFormatDate(date) {
    if (Utils.isBlank(date)) {
      return this.translateService.instant('currentExperience');
    }
    const dateObject = Utils.getDateMMMYY(date);
    return this.translateService.instant(dateObject.month) + ' ' + dateObject.year;
  }

  addItem() {
    const navigationExtras: NavigationExtras = {
      state: {
        itemType: ItemProfileTypes.Experience,
      },
    };
    this.router.navigate(['/add-item'], navigationExtras);
  }

  onEditClick($event) {
    const navigationExtras: NavigationExtras = {
      state: {
        itemType: ItemProfileTypes.Experience,
        id: $event,
      },
    };
    this.router.navigate(['/add-item'], navigationExtras);
  }

  private disconnectObservers(): void {
    this.subscription$.unsubscribe();
  }

  ngOnDestroy() {
    this.disconnectObservers();
  }
}
