import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { NavigationExtras, Router } from '@angular/router';
import { ItemProfileTypes } from '../../../types/item-profile-type';
import { Subscription } from 'rxjs';
import { ISkill } from '../../../../@store/user/user.model';
import { UserState } from '../../../../@store/user/user.state';
import { Store } from '@ngxs/store';
import { DeleteSkillExperience } from '../../../../@store/user/user.actions';
import { TranslateService } from '@ngx-translate/core';
import { AlertController } from '@ionic/angular';
import { Constants } from 'src/app/@global/constants';

@Component({
  selector: 'app-skills',
  templateUrl: './skills.page.html',
  styleUrls: ['./skills.page.scss'],
})
export class SkillsPage implements OnInit, OnDestroy {
  @Input() editable: boolean;
  public items: ISkill[] = [];
  private subscription$: Subscription;
  public maxSkills = Constants.MAX_SKILLS;

  constructor(
    private router: Router,
    private store: Store,
    private translateService: TranslateService,
    public alertController: AlertController
  ) {}

  ngOnInit() {
    this.subscription$ = this.store.select(UserState.getSkillProfile).subscribe((data) => {
      this.items = data;
    });
  }

  addItem() {
    const navigationExtras: NavigationExtras = {
      state: {
        itemType: ItemProfileTypes.Skill,
      },
    };
    this.router.navigate(['/add-item'], navigationExtras);
  }

  private disconnectObservers(): void {
    this.subscription$.unsubscribe();
  }

  ngOnDestroy() {
    this.disconnectObservers();
  }

  public async onDeleteClick(id: string, skillTitle: string) {
    const title = this.translateService.instant(`userProfile.deleteTitle3`);
    const cancelButton = this.translateService.instant(`buttons.cancel`);
    const acceptButton = this.translateService.instant(`buttons.accept`);

    const message = await this.translateService.get('userProfile.deleteMessage', { name: skillTitle }).toPromise();

    const alert = await this.alertController.create({
      cssClass: 'custom-alert',
      header: title,
      message,
      buttons: [
        {
          text: cancelButton,
          role: 'cancel',
          cssClass: 'secondary',
          handler: () => {
            // do nothing
          },
        },
        {
          text: acceptButton,
          handler: () => {
            this.deleteSkill(id);
          },
        },
      ],
    });

    await alert.present();
  }

  public deleteSkill(id: string) {
    this.store.dispatch(new DeleteSkillExperience({ id }));
  }
}
